import loginIcon from  '../assets/login-icon.png'
function Heading({pic}){
      return(
            <div className="website-heading-box">
                  <div className="website-heading">
                        Heading
                  </div>
                  {pic?
                  <div>
                  <img src={pic} alt="img"/>
                  </div>
                  :<img src={loginIcon} alt="img"/>
                  }
                  
            </div>
      )
}
export {Heading}