import React from "react"
function AddQuoteDesc({name}){
      return(
            <div>
                  {name?
                  <React.Fragment>
                        Hello {name}<br/>
                        Add your quote
                  </React.Fragment>
                  :<React.Fragment>
                        Hello 
                  </React.Fragment>
                  }
                  
            </div>
      )
}
export {AddQuoteDesc}