import './App.css';
import React, { useState } from 'react';
import { auth, signInWithGoogle, logout, addQuote2 } from './components/firebase'
import { useAuthState } from 'react-firebase-hooks/auth'
import { Quotes } from './components/quotes';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { AdminPage } from './components/admin';
import { Heading } from './components/heading'
import { AddQuoteDesc } from './components/addQuoteDesc';

function App() {
  const [user] = useAuthState(auth)
  const [quote, setQuote] = useState("")

  function onInputChange(e) {
    setQuote(e.target.value)
  }
  function onButtonClick(e) {
    if (user) {
      addQuote2(quote, user.uid, user.email, user.displayName);
      console.log('quote added for staging')
    }
    else {
      alert('Please sign in')
    }

  }

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
        {user ? <Heading pic={user.photoURL} /> :
          <Heading />
        }
        <br />
        <br />
        <Switch>
          <Route exact path="/">
            <div className="quote-box"></div>

            <div className="input-box">
              <div className="desc">
              {user?<AddQuoteDesc name={user.displayName} />:<AddQuoteDesc/>}
              </div>
              

              <div className="input">
                <input type="text" onChange={onInputChange} placeholder="Your quote"></input>
                <button onClick={onButtonClick}>Submit</button>
              </div>
              <br />
            </div>
            <br /><br /><br />
            {user ? (
              <>
                <label>{user.email}</label><br />
                <label>{user.displayName}</label><br />
                <label>{user.uid}</label><br />
                <img src={user.photoURL} alt="user" /><br />
                <button onClick={logout}>Logout</button>

              </>
            ) :
              (<button onClick={signInWithGoogle}>Login with google</button>)}

            <br />
            <Quotes />
            <Link to="/admin"><button>Admin</button></Link>
            <a href="https://github.com/amalpmathews2003/Quotes-React"><button>Github</button></a>
          </Route>
          <Route exact path="/admin">
            <AdminPage user={user} />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
